@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url(./components/images/pexels-dlxmedia-hu-215591835-11911068.jpg);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}
@media (max-width: 767px) {
  body {
    flex-direction: column;
    padding: 20px;
    background-size: auto;
  }

  h1 {
    font-size: 1.5rem; 
  }

  .TodoWrapper {
    margin-top: 5rem; 
    padding: 1rem; 
  }

  .todo-input, .todo-btn {
    padding: 8px 20px; 
  }

  .todo-input {
    max-width: 100%; 
  }

  .todo-btn {
    max-width: 100%; 
  }

  .Todo {
    flex-direction: column; 
    align-items: flex-start; 
    padding: 0.5rem; 
  }
}


.TodoWrapper {
  background: #1A1A40;
  margin-top: 10rem;
  padding: 2rem;
  border-radius: 30px;
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.todo-input {
  color: white;
  border: 2px solid #8707ff;
  border-radius: 10px;
  padding: 10px 25px;
  background: transparent;
  max-width: 290px;
}

.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  background: #8758ff;
  color: #fff;
  border: 2px solid #8707ff;
  border-radius: 10px;
  padding: 10px 25px;
  cursor: pointer;
  max-width: 190px;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #8758ff;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
